export default {
	name: "NAUI-card",
	data(){
		return{
			
		}
	},
	props: {
		listData: {
			type: Array, 
		},
		points:Number,
		show_times:Number,
		imgurl:String,
		content:String,
		id:Number,
		username:String,
		mark:Boolean,
		anony:Boolean,
		type:String,
		avatarurl:String,
		creat_time:String,
	},
	created:function(e){
		this.listData.creat_time = this.dateTimeFormatter(parseInt(this.listData.creat_time)*1000)
	},
	methods:{
		previewimg(url){//预览卡片图片
		console.log(url)
			// uni.previewImage({
			// 	urls:url,
			// 	indicator: "number",
			// 	loop:false,
			// })
		},
// 		toDetail(id){//卡片详情跳转，信息及详情页自定义
// 			uni.navigateTo({
// 				url:"../expolre/expolre?id="+id
// 			})
// 		},
		dateTimeFormatter (t) {
		  t = new Date(t).getTime()
		  t = new Date(t)
		  var year = t.getFullYear()
		  var month = (t.getMonth() + 1)
		  month = this.checkAddZone(month)
		 
		  var date = t.getDate()
		  date = this.checkAddZone(date)
		 
		  var hour = t.getHours()
		  hour = this.checkAddZone(hour)
		 
		  var min = t.getMinutes()
		  min = this.checkAddZone(min)
		 
		  var se = t.getSeconds()
		  se = this.checkAddZone(se)
		 
		  return year + '-' + month + '-' + date + ' ' + hour + ':' + min + ':' + se
		},
		checkAddZone (num) {
		  return num<10 ? '0' + num.toString() : num
		},
	},
}
